import styled, { useTheme } from "styled-components";
import { SearchIcon } from "../icons";
import type { IButtonSearch } from "./logo.d";

export const ButtonSearch = (p: IButtonSearch) => {
  const { colors } = useTheme();
  return (
    <IconWrapper color={colors.primary} {...p}>
      <ButtonIcon />
    </IconWrapper>
  );
};
const ButtonIcon = styled(SearchIcon)({});
const IconWrapper = styled.div<IButtonSearch>`
  border-radius: 50%;
  width: ${(p) => p.size || 32}px;
  height: ${(p) => p.size || 32}px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: ${(p) => p.color};

  &:hover {
    background-color: rgba(0, 0, 0, 0.1);
  }

  svg: {
    width: ${(p) => (p.size || 32) * 0.5}px;
    height: ${(p) => (p.size || 32) * 0.5}px;
  }
`;
